@charset "UTF-8";
body {
  background-color: #e8e9f4;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#eds_content {
  /* COLORS and TYPOGRAPHY */
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 300;
  font-style: normal;
  /* PAGE LAYOUT */
  min-width: 100%;
  max-width: 100vw; /* Limiter la largeur maximale */
  margin: 0 auto; /* Centrer le contenu */
  padding: 2rem;
  box-sizing: border-box; /* Inclure les paddings et les bordures dans la largeur totale */
  /* VERTICAL SECTIONS */
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: normal;
  align-items: stretch;
  align-content: flex-start;
}
#eds_content .eds-header {
  display: flex;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: 100%;
  align-self: stretch;
  order: 0;
}
#eds_content .eds-header #eds_company {
  display: block;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: 70%;
  align-self: flex-start;
  order: 0;
  font-size: 2rem;
}
#eds_content .eds-header #eds_company .logo {
  border: 1px dotted #000;
  padding: 3px 15px;
}
#eds_content .eds-header #eds_search {
  /* GRID ITEM BEHAVIOR */
  flex-grow: 1;
  /* STYLISED SEARCH */
  display: flex;
  align-items: center;
  border: 1px solid #dedede;
  border-radius: 25px;
  padding: 8px;
  max-height: 2rem;
  background-color: #fff;
  border: 1px solid #aaa;
  box-sizing: border-box; /* Inclure les paddings et les bordures dans la largeur totale */
}
#eds_content .eds-header #eds_search label {
  margin-right: 10px;
  padding-left: 10px;
}
#eds_content .eds-header #eds_search input {
  border: none;
  outline: none;
  flex-grow: 1;
}
#eds_content .eds-header #eds_search .search-icon {
  margin-left: 10px;
  margin-right: 10px;
}
#eds_content .eds-body {
  display: flex;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: auto;
  align-self: auto;
  order: 0;
  border-radius: 1rem;
  /* INNER CONTAINER */
  background: #fff;
  border: 1px solid #afafaf;
  margin: 2rem 0;
  padding: 0;
}
#eds_content .eds-body #eds_menu {
  background-color: #f4f5ff;
  font-family: "IBM Plex Sans", sans-serif;
  padding-bottom: 5rem;
  width: 368px;
  border-radius: 1rem 0 0 1rem;
  height: 100%; /* Assurez-vous que le menu occupe toute la hauteur */
  box-sizing: border-box; /* Inclure les paddings et les bordures dans la hauteur totale */
}
#eds_content .eds-body #eds_menu .eds-dashboard-btn {
  display: block;
  color: #cccccc;
  font-size: 24px;
  font-weight: 400;
  text-align: center;
  height: 64px;
  width: 64px;
  padding: 1rem;
  margin-bottom: 1rem;
  cursor: pointer;
}
#eds_content .eds-body #eds_menu .eds-dashboard-btn .eds-icon-background {
  fill: #b16ace;
}
#eds_content .eds-body #eds_menu .eds-dashboard-btn .eds-icon-overlay {
  fill: #e8e9f4;
}
#eds_content .eds-body #eds_menu .eds-dashboard-btn:hover .eds-icon-background {
  fill: #000000;
}
#eds_content .eds-body #eds_menu .eds-dashboard-btn:hover .eds-icon-overlay {
  fill: #ffffff;
}
#eds_content .eds-body #eds_menu .layout {
  z-index: 1;
}
#eds_content .eds-body #eds_menu .layout .header {
  display: flex;
  align-items: center;
  padding: 20px;
}
#eds_content .eds-body #eds_menu .layout .content {
  padding: 12px 50px;
  display: flex;
  flex-direction: column;
}
#eds_content .eds-body #eds_menu .layout .footer {
  text-align: center;
  margin-top: auto;
  margin-bottom: 20px;
  padding: 20px;
}
#eds_content .eds-body #eds_menu .sidebar {
  color: #7d84ab;
  overflow-x: hidden !important;
  position: relative;
}
#eds_content .eds-body #eds_menu .sidebar::-webkit-scrollbar-thumb {
  border-radius: 4px;
}
#eds_content .eds-body #eds_menu .sidebar:hover::-webkit-scrollbar-thumb {
  background-color: #1a4173;
}
#eds_content .eds-body #eds_menu .sidebar::-webkit-scrollbar {
  width: 6px;
  background-color: #0c1e35;
}
#eds_content .eds-body #eds_menu .sidebar .image-wrapper {
  overflow: hidden;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  display: none;
}
#eds_content .eds-body #eds_menu .sidebar .image-wrapper > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
#eds_content .eds-body #eds_menu .sidebar.has-bg-image .image-wrapper {
  display: block;
}
#eds_content .eds-body #eds_menu .sidebar .sidebar-layout {
  height: auto;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: #0c1e35;
  z-index: 2;
}
#eds_content .eds-body #eds_menu .sidebar .sidebar-layout .sidebar-header {
  height: 100px;
  min-height: 100px;
  display: flex;
  align-items: center;
  padding: 0 20px;
}
#eds_content .eds-body #eds_menu .sidebar .sidebar-layout .sidebar-header > span {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
#eds_content .eds-body #eds_menu .sidebar .sidebar-layout .sidebar-content {
  flex-grow: 1;
  padding: 10px 0;
}
#eds_content .eds-body #eds_menu .sidebar .sidebar-layout .sidebar-footer {
  height: 230px;
  min-height: 230px;
  display: flex;
  align-items: center;
  padding: 0 20px;
}
#eds_content .eds-body #eds_menu .sidebar .sidebar-layout .sidebar-footer > span {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
@keyframes swing {
  0%, 30%, 50%, 70%, 100% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(10deg);
  }
  40% {
    transform: rotate(-10deg);
  }
  60% {
    transform: rotate(5deg);
  }
  80% {
    transform: rotate(-5deg);
  }
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-header {
  font-weight: 600;
  padding: 10px 25px;
  font-size: 0.8em;
  letter-spacing: 2px;
  transition: opacity 0.3s;
  opacity: 0.5;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item a {
  display: flex;
  align-items: center;
  height: 50px;
  padding: 0 20px;
  color: #7d84ab;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item a .menu-icon {
  font-size: 1.2rem;
  width: 35px;
  min-width: 35px;
  height: 35px;
  line-height: 35px;
  text-align: center;
  display: inline-block;
  margin-right: 10px;
  border-radius: 2px;
  transition: color 0.3s;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item a .menu-icon i {
  display: inline-block;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item a .menu-title {
  font-size: 0.9em;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  flex-grow: 1;
  transition: color 0.3s;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item a .menu-prefix,
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item a .menu-suffix {
  display: inline-block;
  padding: 5px;
  opacity: 1;
  transition: opacity 0.3s;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item a:hover .menu-title {
  color: #dee2ec;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item a:hover .menu-icon {
  color: #dee2ec;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item a:hover .menu-icon i {
  animation: swing ease-in-out 0.5s 1 alternate;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item a:hover::after {
  border-color: #dee2ec !important;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item.sub-menu {
  position: relative;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item.sub-menu > a::after {
  content: "";
  transition: transform 0.3s;
  border-right: 2px solid currentcolor;
  border-bottom: 2px solid currentcolor;
  width: 5px;
  height: 5px;
  transform: rotate(-45deg);
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item.sub-menu > .sub-menu-list {
  padding-left: 20px;
  display: none;
  overflow: hidden;
  z-index: 999;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item.sub-menu.open > a {
  color: #dee2ec;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item.sub-menu.open > a::after {
  transform: rotate(45deg);
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item.active > a .menu-title {
  color: #dee2ec;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item.active > a::after {
  border-color: #dee2ec;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu .menu-item.active > a .menu-icon {
  color: #dee2ec;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu > ul > .sub-menu > .sub-menu-list {
  background-color: #0b1a2c;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu.icon-shape-circle .menu-item a .menu-icon, #eds_content .eds-body #eds_menu .layout .sidebar .menu.icon-shape-rounded .menu-item a .menu-icon, #eds_content .eds-body #eds_menu .layout .sidebar .menu.icon-shape-square .menu-item a .menu-icon {
  background-color: #0b1a2c;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu.icon-shape-circle .menu-item a .menu-icon {
  border-radius: 50%;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu.icon-shape-rounded .menu-item a .menu-icon {
  border-radius: 4px;
}
#eds_content .eds-body #eds_menu .layout .sidebar .menu.icon-shape-square .menu-item a .menu-icon {
  border-radius: 0;
}
#eds_content .eds-body #eds_menu .layout .sidebar:not(.collapsed) .menu > ul > .menu-item.sub-menu > .sub-menu-list {
  visibility: visible !important;
  position: static !important;
  transform: translate(0, 0) !important;
}
#eds_content .eds-body #eds_menu .layout .sidebar.collapsed .menu > ul > .menu-header {
  opacity: 0;
}
#eds_content .eds-body #eds_menu .layout .sidebar.collapsed .menu > ul > .menu-item > a .menu-prefix,
#eds_content .eds-body #eds_menu .layout .sidebar.collapsed .menu > ul > .menu-item > a .menu-suffix {
  opacity: 0;
}
#eds_content .eds-body #eds_menu .layout .sidebar.collapsed .menu > ul > .menu-item.sub-menu > a::after {
  content: "";
  width: 5px;
  height: 5px;
  background-color: currentcolor;
  border-radius: 50%;
  display: inline-block;
  position: absolute;
  right: 10px;
  top: 50%;
  border: none;
  transform: translateY(-50%);
}
#eds_content .eds-body #eds_menu .layout .sidebar.collapsed .menu > ul > .menu-item.sub-menu > a:hover::after {
  background-color: #dee2ec;
}
#eds_content .eds-body #eds_menu .layout .sidebar.collapsed .menu > ul > .menu-item.sub-menu > .sub-menu-list {
  transition: none !important;
  width: 200px;
  margin-left: 3px !important;
  border-radius: 4px;
  display: block !important;
}
#eds_content .eds-body #eds_menu .layout .sidebar.collapsed .menu > ul > .menu-item.active > a::after {
  background-color: #dee2ec;
}
#eds_content .eds-body #eds_menu .layout .sidebar.has-bg-image .menu.icon-shape-circle .menu-item a .menu-icon, #eds_content .eds-body #eds_menu .layout .sidebar.has-bg-image .menu.icon-shape-rounded .menu-item a .menu-icon, #eds_content .eds-body #eds_menu .layout .sidebar.has-bg-image .menu.icon-shape-square .menu-item a .menu-icon {
  background-color: rgba(11, 26, 44, 0.6);
}
#eds_content .eds-body #eds_menu .layout .sidebar.has-bg-image:not(.collapsed) .menu > ul > .sub-menu > .sub-menu-list {
  background-color: rgba(11, 26, 44, 0.6);
}
#eds_content .eds-body #eds_menu .layout.rtl .sidebar .menu .menu-item a .menu-icon {
  margin-left: 10px;
  margin-right: 0;
}
#eds_content .eds-body #eds_menu .layout.rtl .sidebar .menu .menu-item.sub-menu > a::after {
  transform: rotate(135deg);
}
#eds_content .eds-body #eds_menu .layout.rtl .sidebar .menu .menu-item.sub-menu > .sub-menu-list {
  padding-left: 0;
  padding-right: 20px;
}
#eds_content .eds-body #eds_menu .layout.rtl .sidebar .menu .menu-item.sub-menu.open > a::after {
  transform: rotate(45deg);
}
#eds_content .eds-body #eds_menu .layout.rtl .sidebar.collapsed .menu > ul > .menu-item.sub-menu a::after {
  right: auto;
  left: 10px;
}
#eds_content .eds-body #eds_menu .layout.rtl .sidebar.collapsed .menu > ul > .menu-item.sub-menu > .sub-menu-list {
  margin-left: -3px !important;
}
